// src/components/Chat.js
import React, { useState, useEffect, useRef } from 'react';
import axios from 'axios';
import '../App.css';
import { useAuth } from './AuthContext';

function mergePunctuations(arr) {
  const result = [];
  let tempWord = ['', ''];

  for (const [fr, en] of arr) {
    if ('.,!?'.includes(fr)) {
      tempWord[0] += fr;
      tempWord[1] += en;
    } else {
      if (tempWord[0]) {
        result.push(tempWord);
        tempWord = ['', ''];
      }
      tempWord = [fr, en];
    }
  }

  if (tempWord[0]) {
    result.push(tempWord);
  }

  return result;
}

const Chat = ({ temperature, maxTokens }) => {
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState('');
  const [systemMessage, setSystemMessage] = useState('');
  const messageEndRef = useRef(null);
  const [user, setUser] = useState(null);
  const { token } = useAuth();

  useEffect(() => {
    const storedUser = localStorage.getItem('user');
    if (storedUser) {
      setUser(JSON.parse(storedUser));
    }
  }, []);

  useEffect(() => {
    if (user) {
      setMessages([{
        role: 'assistant',
        content: 'Hello, I am GPT-4. AMA.'
      }]);
    }
  }, [user]);

  useEffect(() => {
    if (messageEndRef.current) {
      messageEndRef.current.scrollIntoView({ behavior: 'smooth' });
    }
  }, [messages]);

  const sendMessage = async () => {
    if (!input.trim()) return;

    const userMessage = { role: 'user', content: input };
    setMessages(prevMessages => [...prevMessages, userMessage]);
    setInput('');

    try {
      const response = await axios.post('https://asia-south1-ppt-tts.cloudfunctions.net/ai-backend/chat', {
        messages: [...messages, userMessage],
        systemMessage,
        temperature: parseFloat(temperature),
        maxTokens: parseInt(maxTokens)
      }, {
        headers: {
          'Authorization': `Bearer ${token}`,
        },
      });
      console.log('Chat data:', token);
      console.log('Server response:', response.data);
      setMessages(prevMessages => [...prevMessages, response.data]);
    } catch (error) {
      console.error("Error sending message:", error);
    }
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      sendMessage();
    }
  };

  const renderMessage = (content) => {
    if (typeof content === 'string') {
      return <p>{content}</p>;
    }

    return content.map((part, idx) => {
      if (typeof part === 'string' && part.startsWith('[Correction]')) {
        return <p key={idx} className="correction">{part.replace('[Correction]', '').trim()}</p>;
      } else if (typeof part === 'string' && part.startsWith('[Response]')) {
        try {
          let wordTranslationsString = part.replace(/\\u([\dA-Fa-f]{4})/g, (match, grp) => String.fromCharCode(parseInt(grp, 16))).replace('[Response]', '').trim().replace(/\(/g, '[').replace(/\)/g, ']').replace(/\\"/g, '').replace(/\\/g, '').replace(/,\s*\[/g,'--\[').replace(/,/g, '--').replace(/----/g, ',--').replace(/,--\s*--/g, ',--,').replace(/--\]/g,',]').slice(1,-1);
          let outerArray = wordTranslationsString.slice(2, -2).split(']--[');
          let splitArray = mergePunctuations(outerArray.map(element => element.split('--')));
          console.log(wordTranslationsString);
          console.log(splitArray);

          return (
            <p key={idx} className="text-container">
              {splitArray.map(([word, translation], wIdx) => (
                <span key={wIdx} className="hoverable-word">{word + " "}
                  <span className="tooltiptext badge badge-light">{translation}</span>
                </span>
              ))}
            </p>
          );
        } catch (error) {
          console.error("Error parsing response:", error);

          return <p key={idx}>{part}</p>;
        }
      } else if (typeof part === 'string' && part.startsWith('[Translation]')) {
        return <p key={idx} className="translation">{part.replace('[Translation]', '').trim()}</p>;
      }
      return <p key={idx}>{part}</p>;
    });
  };

  return (
    <div className="chat-container">
      <h1>Conversation Tool</h1>
      <hr />
      
      <div className="p-4 mb-4 border rounded">
        <h2 className="text-lg font-semibold mb-2">System Message</h2>
        <textarea
          className="w-full p-2 border rounded"
          value={systemMessage}
          onChange={(e) => setSystemMessage(e.target.value)}
          placeholder="Enter system message here..."
          rows="3"
        />
      </div>

      <div className="message-container">
        {messages.map((msg, index) => (
          <div key={index} className={`message-row ${msg.role}-row`}>
            {msg.role === 'assistant' && (
              <div className="profile-picture-container">
                <img src='logo192.png' alt={`${msg.role} profile picture`} className={`profile-picture`} />
              </div>
            )}
            <div className={`message ${msg.role}`}>
              {renderMessage(msg.content)}
            </div>
            {msg.role !== 'assistant' && (
              <div className="profile-picture-container">
                <img src='flame_img.jpeg' alt={`${msg.role} profile picture`} className={`profile-picture`} />
              </div>
            )}
          </div>
        ))}
        <div ref={messageEndRef} />
      </div>

      <div className="input-container">
        <input
          type="text"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          onKeyDown={handleKeyPress}
          placeholder="Type your message..."
        />
      </div>
    </div>
  );
};

export default Chat;
